@import 'variables';

.main {
    &__item {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        min-height: calc( 100vh - 230px );
        &__card {
            display: flex;
            flex-direction: column;
            align-items: center;
            &__name {
                align-self: flex-start;
                font-family: 'main';
                font-size: 40px;
                font-weight: 700;
                text-transform: uppercase;
                color: $secondary-color; }
            &__container {
                display: flex;
                justify-content: space-evenly;
                align-items: center;
                &__img {
                    width: 30vw;
                    &:hover {} }
                &__features {
                    max-width: 50vw;
                    min-width: 40vw;
                    background-color: #c2bebe; } } } } }
