@font-face {
    font-family: 'main';
    src: url(../fonts/MullerRegular.otf); }

@font-face {
    font-family: 'mainBold';
    src: url(../fonts/MullerBold.otf); }

@font-face {
    font-family: 'mainExtraBold';
    src: url(../fonts/MullerExtraBold.otf); }

@font-face {
    font-family: 'mainLight';
    src: url(../fonts/MullerLight.otf); }

@font-face {
    font-family: 'mainUltraLight';
    src: url(../fonts/MullerUltraLight.otf); }

@font-face {
    font-family: 'decorative';
    src: url(../fonts/IntroRustG-Base.otf); }


$main-color: #FFCB03;
$secondary-color: #232020;

@mixin transition {
    transition: all;
    transition-duration: 0.5s; }


@mixin button {
    height: 46px;
    width: fit-content;
    padding: 0px 30px;
    background-color: $main-color;
    color: #000000;
    border-radius: 30px;
    border: none;
    font-family: 'main';
    text-transform: uppercase;
    @include transition;
    &:hover {
        background-color: #C17D17;
        color: #FFFFFF;
        box-shadow: inset 2px 4px 6px rgba(0, 0, 0, 0.25);
        @include transition; } }




@mixin underline {
    border-bottom: 3px solid $main-color;
    width: 80px;
    padding-top: 16px; }

@mixin photo_block {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    max-width: 100%;
    min-width: 100%;
    min-height: calc( 100vh - 80px );
    max-height: calc( 100vh - 80px ); }

@mixin photo_block_h2 {
    font-family: 'decorative';
    font-size: 70px;
    color: #fff;
    position: absolute;
    text-align: center; }


::-webkit-scrollbar {
    width: 10px; }

::-webkit-scrollbar-track {
   background-color: $secondary-color; }

::-webkit-scrollbar-thumb {
    background-color: $main-color;
    border-radius: 10px; }


